@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap");

:root {
  /* colors */
  --col1: rgb(143, 20, 40);
  --col2: rgb(255, 248, 237);
  --col3: rgb(40, 40, 40);
  --colwhite: #ffffff;
  --colred: #ea1515;
  --colblue: #246fbf;
  --colgreen: rgb(49, 116, 49);

  /* fonts */
  --f-qs: "Quicksand", sans-serif;
}
* {
  font-family: var(--f-qs);
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  color: var(--col3);
}
html {
  background-color: var(--col3);
}

html,
body {
  padding: 0;
  margin: 0;
}

::selection {
  background-color: var(--col1);
  color: white;
}
::placeholder{
  color:rgba(0,0,0,0.35);
}


/* tooltip */
.tooltip-container {
  position: relative;
  display: inline-block;
  cursor: help;
}

.tooltip-text {
  visibility: hidden;
  width: 200px;
  background-color: var(--col3);
  color: white;
  text-align: center;
  padding: 5px;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  margin-left: -80px;
  font-size:0.7rem;
}

.tooltip-container:hover .tooltip-text {
  visibility: visible;
}

/********** padding, margins, others **********/

.yaui .m1 {
  margin: 1em;
}

.yaui .p1 {
  padding: 1em;
}

.yaui .flex {
  display: flex;
}
.yaui .i-flex {
  display: inline-flex;
}

.yaui .flexdir-col {
  flex-direction: column;
}

.yaui .jf-center {
  justify-content: center;
}
.yaui .ai-center {
  align-items: center;
}

.yaui .blur3 {
  filter: blur(3px);
  -webkit-filter: blur(3px);
}

.yaui img {
  max-width: 100%;
}

/********** containers **********/

.yaui-ctn {
  background-color: rgba(255, 248, 237, 0.8);
  border-radius: 7px;
  box-shadow: 0 4px 7px rgba(0, 0, 0, 0.5);
}

d-only {
  display: none;
}

/* for screens bigger than 767px */
@media only screen and (min-width: 767px) {
  .yaui-ctn {
    width: auto;
  }
  m-only {
    display: none;
  }
  d-only {
    display: inline;
  }
}
/********** forms **********/

.yaui-form {
  padding: 1em;
}

.yaui-form * {
  margin-top: 1em;
  transition: 0.25s;
}

.yaui-form label {
  font-weight: 500;
}
.yaui-form input {
  width: 100%;
  font-size: 1.25em;
  border-radius: 6px;
  outline: none;
  border: none;
  box-shadow: 0 3px 2px rgba(0, 0, 0, 0.5);
  padding: 1rem;
  box-sizing: border-box;
  transition: 0.25s;
}

.yaui-form input:focus {
  box-shadow: 0 5px 2px rgba(143, 20, 40, 0.8);
  font-weight: 500;
}

.yaui-form input.error {
  outline: 2px solid var(--colred);
  box-shadow: 0 1px 5px 5px var(--colred);
  font-size: 1rem;
}
.yaui-form .inputError{
  color: var(--colred);
  display:block;
  margin:0;
  padding:0;
  font-size:clamp(0.85rem, 2em, 0.75vw);
}

.hl-placeholder::placeholder {
  color: var(--col1);
  font-size: 0.8rem;
}

.yaui-form .btn1 {
  cursor: pointer;
  color: var(--colwhite);
  background-color: var(--col1);

  width: 100%;
  border-radius: 5px;
  padding: 0.5em 0;
  outline: none;
  border: none;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.5);

  font-weight: bold;
  font-size: 1.75em;
  transition: 0.25s;
}

.yaui-form .btn1:active {
  background-color: rgb(69, 0, 0);
}



/* others */

.yaui-popup {
  position: fixed;
  background-color: var(--col2);
}

span.line-header {
  display: block;
  color: var(--col1);
  border-bottom: 1px solid var(--col1);
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
  margin: 1rem 0 1rem 0;
  font-weight: 500;
}
